import React, { useEffect } from 'react';
import { Button,Offcanvas,Form } from 'react-bootstrap';
import { useState } from 'react';
import { PriceContext } from '../Context/LoginContext';
import { useContext } from 'react';
export default function Sidebar() {
    const{priceRangeOne,priceRangeTwo,setPriceFilter}=useContext(PriceContext)
    const [show, setShow] = useState(false);
  
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    const options=[{
      label:"200-400",value:"1"
    },{
      label:"400-600",value:"2"
    },{
      label:"600-800",value:"3"
    },{
      label:"800-1000",value:"4"
    }]

    const[selctedValue,setSelectedValue]=useState("")
    if(selctedValue==="1"){
      setPriceFilter({priceRangeOne:parseInt("200"),priceRangeTwo:parseInt("400")});
      console.log("PriceFilter: " +priceRangeOne +"priceRangeTwo"+ priceRangeTwo)}
      else if(selctedValue==="2"){
        setPriceFilter({priceRangeOne:parseInt("400"),priceRangeTwo:parseInt("600")});
        console.log("PriceFilter: " +priceRangeOne +"priceRangeTwo"+ priceRangeTwo)}
        else if(selctedValue==="3"){
      setPriceFilter({priceRangeOne:parseInt("600"),priceRangeTwo:parseInt("800")});
      console.log("PriceFilter: " +priceRangeOne +"priceRangeTwo"+ priceRangeTwo)}
          else if(selctedValue==="4"){
            setPriceFilter({priceRangeOne:parseInt("800"),priceRangeTwo:parseInt("1000")});
            console.log("PriceFilter: " +priceRangeOne +"priceRangeTwo"+ priceRangeTwo)}
  let filterdata=[]
            useEffect(()=>{
            let data= JSON.parse(localStorage.getItem("ProductsData"));
            console.log("data  :: " +data)
            filterdata = data.filter((searchVal) => {
              return searchVal.price>=priceRangeOne && searchVal.price<=priceRangeTwo;
            })
            console.log("filterdata price : "+JSON.stringify(filterdata))

          })
  return (
      <div>
           <>
           <center>
        <Button variant="primary" className='text-center w-25' onClick={handleShow}>
          Filter
        </Button>
        </center>
        <Offcanvas show={show} onHide={handleClose}>
          <Offcanvas.Header closeButton>
            <Offcanvas.Title>Price</Offcanvas.Title>
          </Offcanvas.Header>
          <Offcanvas.Body>
          <select name='priceRange' aria-label="Default select example"
          onChange={(e)=>{ console.log("Price Range:"+e.target.value);
          setSelectedValue(e.target.value)}}>

        <option>Select the Price</option>
      {options.map((option)=>{
        return (
<option key ={option.value} value={option.value}>{option.label}</option>)
      })}
        </select>
        <p> </p>
          </Offcanvas.Body>
        </Offcanvas>
      </>
      </div>
  )
  
}


