import React from 'react';
import Topbar from '../components/Topbar';
import Carouselpart from '../components/Carouselpart';
import Navitems from '../components/Navitems';
import Products from '../components/Products';
import { ProductContext, PriceContext } from '../Context/LoginContext'
import Sidebar from '../components/Sidebar';
import { useState } from 'react';
function Homepage() {
    const [ProductDetail, setProductDetail] = useState({
        serachKey:"",prdsData:""
    });
    const [PriceFilter, setPriceFilter] = useState({
        priceRangeOne: 0, priceRangeTwo: 0
    });
    console.log("search key : " + ProductDetail.serachKey);
    console.log("priceRange homepage : " + PriceFilter.priceRangeOne)
    return (
        <div>
            <ProductContext.Provider value={{prdsData:ProductDetail.prdsData,serachKey:ProductDetail.serachKey, setProductDetail }}>
            <Topbar />           
            <Carouselpart />
            <Navitems />
            <PriceContext.Provider value={{ priceRangeOne: PriceFilter.priceRangeOne,priceRangeTwo: PriceFilter.priceRangeTwo, setPriceFilter }}>
            <Sidebar />
                      
            <Products />
            </PriceContext.Provider> 
            </ProductContext.Provider>
             
        </div>

    )

}

export default Homepage;
